.todo{
  line-height: 1.2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__left{
    display: flex;
    gap: 20px;
  }
  &__checkbox{
    flex: 0 0 20px;
    .Checkbox{
      margin-top: 2px;
    }
  }
  &__content{
    display: flex;
    flex-wrap: wrap;
  }
  &__name{
    flex: 1 0 100%;
    font-size: 18px;
    font-weight: 600;
  }
  &__desc{
    flex: 1 0 100%;
    font-size: 15px;
    margin-top: 2px;
    color: #a8a7ac;
  }
  &__date{
    margin-top: 4px;
    margin-right: 10px;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 5px;
    color: #555457;

    &-icon{
      height: 15px;
      width: 15px;
      svg{
        width: 100%;
        height: 100%;
        object-fit: contain;

        *{
          fill: #555457;
        }
      }
    }

    &.red{
      color: #D04143;

      svg *{
        fill: #D04143;
      }
    }
  }


  &:not(:last-child){
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: #EFEEF5 1px solid;
  }

  &__right{

  }

  &__delete{
    padding: 5px;
    box-sizing: content-box;
    &:hover svg *{
      fill: #ED2939;
    }
    svg{
      width: 25px;
      height: 25px;
      *{
      }
    }
  }
  
}