.ToDoList{
  width: 600px;
  background-color: #fff;
  
  border-radius: 20px;
  box-shadow: #fff 10px 10px;
  box-shadow: 0px 0px 15px 5px rgb(222, 221, 230);
  & > *:first-child{
    padding: 30px 30px 0px 30px;
  }
  & > *:last-child{
    padding: 20px 30px 25px 30px;
  }
  &__bottom{
    margin-top: 15px;
    border-top: #EFEEF5 1px solid;
    
    &__row{
      display: flex;
      justify-content: space-between;
      position: relative;
    }
  }

  &__add{
    position: absolute;
    box-sizing: content-box;
    display: flex;
    flex-direction: column;
    transition: all 0.35s;
    white-space: nowrap;
    background-color: transparent;

    border-radius: 0px;
    width: calc(100% + 80px);
    right: 0%;
    top: 50%;
    transform: translate(0%, -50%);
    
    padding: 0px;
    z-index: 0;
    box-shadow: 0px 0px 15px 5px rgba(222, 221, 230, 0);
    
    &-title{
      font-weight: 600;
      transition: all 0.25s;
      &.error{
        &::placeholder{
          color: #db4649;
        }
      }
    }
    &-description{
      font-size: 16px;
    }
    
    &.active{
      background: #fff;
      z-index: 2;
      transform: translate(50%, -50%);
      right: 50%;
      border-radius: 15px;
      padding: 25px;
      
      box-shadow: 0px 0px 15px 5px rgb(222, 221, 230);



      .ToDoList__add-top{
        height: 53px;
        opacity: 1;
      }
      .ToDoList__add-bottom{
        margin-top: 50px;
      }
      .ToDoList__add-cancel{
        opacity: 1;
        pointer-events: all;
      }
      .ToDoList__add-date{
        opacity: 1;
        pointer-events: all;
      }

    }

    &-cancel{
      opacity: 0;
      pointer-events: none;
      transition: all 0.35s;
    }

    &-date{
      opacity: 0;
      pointer-events: none;
      transition: all 0.35s;
      display: flex;
      align-items: center;
      gap: 5px  ;
      svg{
        width: 17px;
        height: 17px;
        *{
          transition: all 0.25s;
          fill: rgb(156, 155, 160);
        }
      }
      &__wrapper{
        flex: 1 0 auto;
      }

      input{
        max-width: 90px;
        color: #6d6c72;
        transition: all 0.25s;
      }

      &.error{
        border: 1px solid #db4649;
        animation: shake 0.35s;
        
        input{
          &::placeholder{
            color: #db4649;
          }
        }
        svg *{
          fill: #db4649;
        }
      }
    }

    &-top{
      opacity: 0;
      height: 0;
      overflow: hidden;
      transition: all 0.35s;
      display: flex;
      flex-direction: column;

      input{
        transition: all 0.25s;
        line-height: 1.5;
        &:not(:last-child){
          margin-bottom: 5px;
        }
      }
    }

    &-bottom{
      transition: all 0.35s;
      margin-top: 0px;
      display: flex;
      gap: 15px;
      
    }

    &-button{
      transition: 1s;
      padding: 2px 15px;
      border-radius: 5px;
      font-weight: 600;
      color: #fff;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      gap: 5px;
      background-color: #41644A;

      &:hover{
        background-color: #2f5038;
      }

      &:active{
        background-color: #41644ade;
      }
    }
  }

  &__delete{
    position: relative;
    z-index: 1;
    background: #ED2939;
    padding: 4px 15px;
    border-radius: 5px;
    font-weight: 600;
    color: #fff;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 5px;
    
    &:hover{
      background: #db4649;
    }

    &:active{
      background: #d57173;
    }
  }
}

@keyframes shake {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(6deg); }
  50% { transform: rotate(-6deg); }
  75% { transform: rotate(6deg); }
  100% { transform: rotate(0deg); }
}